.App {
  text-align: center;
}

.nav{
  color: #fff !important;
}

.header{
  width: 100%;
  height: 30vh;
  display: flex;
  flex-direction: row;
  align-items: justify;
  justify-content:space-between;
  background: rgb(153, 72, 253);
  color: #fff !important;
}
.header img{
  width: 40vh;
}

.header > div {
  width: 50%;
  height: 30vh;
}

.tabSvg{
  min-width:1rem !important;
  max-width:2rem !important;
}
.titleType{
  margin: 10px 0px;
  text-transform: uppercase !important;
  color: rgb(57,28,131);
}

/* 
// Small devices (landscape phones, 576px and up) */
@media (min-width: 276px) {
  .svgImage{
    position: absolute;
    opacity: 0.2;
    margin-left: 5%;
    margin-top: -10px;
  }

 }

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) { 
  .svgImage{
    position: static;
    opacity: 1;
  }
  .btnMore{
    right:0px;
  }
  
 }
/* 
// Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .svgImage{
    position: static;
    opacity: 1;
  }
  .btnMore{
    position: relative;
    width: 50px !important;
    height: 100px !important;
    right:-60px;
  }
 }

/* // Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) { 
  .container-fluid{
    width: 60% !important;
  }
  .svgImage{
    position: static;
    opacity: 1;
  }
  .btnMore{
    position: relative;
    width: 50px !important;
    height: 100px !important;
    right:-60px;
  }
  
 }

